import { ADD_TODO, EDIT__TODO, REDUCE__TODO,STATUS__TODO } from "./actionTypes"

const addTodo=(payload)=>{
    return{
        type:ADD_TODO,
        payload
    }
}
const reduceTodo=(id)=>{
    return{
        type:REDUCE__TODO,
        payload:{
            id
        }
    }
}
const statusTodo=(id)=>{
    return{
        type:STATUS__TODO,
        payload:{
            id
        }
    }
}
const editTodo=(payload)=>{
    return{
        type:EDIT__TODO,
        payload
    }
}

export {addTodo,reduceTodo,statusTodo,editTodo}