import React from 'react'
import { Redirect, useHistory, useParams } from 'react-router'
import {useDispatch,useSelector} from "react-redux"
import { editTodo } from '../Redux/action'
import style from "../Components/todo.module.css"

export default function Edit() {
     const dispatch = useDispatch()
     const history=useHistory()
    const {id}=useParams()
    const todo = useSelector(state => state.todo)
    let singleVal=""
    todo.map((item)=>{
        if(item.id==id){
            singleVal=item.title
        }
    })
    const [editValue,setEditValue] = React.useState(singleVal)
    const handleSubmit=(id)=>{
        const payload={
            id,
            editValue
        }
        dispatch(editTodo(payload))
        history.push("/add-details")
        

    }
    return (
        <div className={style.edit_2}>
         
            <input type="text" placeholder="edit" name="editValue" value={editValue} onChange={(e)=>setEditValue(e.target.value)} />
            <button onClick={()=>handleSubmit(id)}>submit</button>
        </div>
    )
}
