import { ADD_TODO, EDIT__TODO, REDUCE__TODO, STATUS__TODO } from "./actionTypes";

const initState = {
  total: 0,
  todo: [
    {
      id: 1,
      title: "react",
      status: false,
    },
  ],
};
const reducer = (state = initState, { type, payload }) => {
  switch (type) {
    case ADD_TODO: {
      return {
        ...state,
        todo: [...state.todo, payload],
      };
    }
    case REDUCE__TODO: {
      const updatedTodo = state.todo.filter((item) => item.id != payload.id);
      let total_temp=updatedTodo.filter((item)=>item.status).length
      return {
        ...state,
        todo: updatedTodo,
        total:total_temp
        
      };
    }
    case STATUS__TODO: {
      const statusTodo = state.todo.map((item) => {
        let temp = item;
        if (item.id === payload.id) {
          item.status = !item.status;
        }
        return temp;
      });
      let total_temp=statusTodo.filter((item)=>item.status).length
      return {
        ...state,
        todo: statusTodo,
        total:total_temp
      };
    }
    case EDIT__TODO: {
        var changeTodo=state.todo.map((item)=>{
            let  edit_temp=item;
            if(item.id===payload.id){
                item.title=payload.editValue
            }
            return edit_temp
        })
        
        return {
          ...state,
          todo: changeTodo
        };
      }
    
    default: {
      return state;
    }
  }
};

export { reducer };
