import React from 'react'
import {useDispatch} from "react-redux"
import {v4 as uuid} from "uuid"
import { addTodo, reduceTodo, statusTodo } from '../Redux/action'
import {useSelector} from "react-redux"
import { Link } from 'react-router-dom'
import style from "./todo.module.css"

export default function TodoInput() {
    const todo = useSelector(state => state.todo)
    const total = useSelector(state => state.total)
    const [title,setTitle] = React.useState("")
    console.log(title)
     const dispatch = useDispatch()

    const handleAdd=()=>{
        const payload={
            id:uuid(),
            title:title,
            status:false
        }
        const addAction=addTodo(payload)
        dispatch(addAction)

    }
    const handleDel=(id)=>{
        const delAction=reduceTodo(id)
        dispatch(delAction)

    }
    const handleToggle=(id)=>{
      dispatch(statusTodo(id))

    }
    
    return (
        <>

        <div className={style.todo}>
        <div>CompletedTasks - <span className={style.total}>{total}</span></div>

            <div className={style.input}>
                <input type="text" placeholder="add something" name="title" value={title} onChange={(e)=>{setTitle(e.target.value)}} />
                <button onClick={handleAdd}>ADD</button>
            </div>

            <div>
                {todo?.map((item)=>
                <div  className={style.list}>
                    <div>{`${item.title}`}</div>
                    <div className={style.list_2}>
                        <div> <button onClick={()=>handleToggle(item.id)} >{`${item.status}`}</button></div>
                        <div> <button onClick={()=>handleDel(item.id)}>delete</button></div>
                        <div><Link to={"/todo/"+item.id}><button className={style.edit}>edit</button></Link></div>
                    </div>
                </div>
                )}
               
                
                </div>
            </div>
            </>
    )
}

